import {
    MAP_INIT,
    CHANGED_MAP_SOURCE,
    MAPPING_INIT,
    UPDATE_MAP_SIZE,
    SET_SELECT,
    CHANGED_MAP_SELECT,
    INIT_MAP_SELECT,
} from '../constants/Map';

const initialState = {
    source: [],
    width: 0,
    height: 0,
    mapping: {},
    select: false,
    selectedItem: []
};

export default function map(state = initialState, action) {
    switch (action.type) {
        case MAP_INIT:{
            return {
                ...state,
                source: action.payload.source,
                width: action.payload.width,
                height: action.payload.height
            };
        }
        case MAPPING_INIT:
            return {
                ...state,
                mapping: action.payload
            };
        case CHANGED_MAP_SOURCE:
            return {...state, source: action.payload };
        case UPDATE_MAP_SIZE:
            return {...state, width: action.payload.newWidth, height: action.payload.newHeight};
        case SET_SELECT:
            return {...state, select: action.payload};
        case CHANGED_MAP_SELECT:
            return {...state, selectedItem: action.payload};
        case 'RESET_SELECTED_ITEMS':
            return {...state, selectedItem: action.payload};
        default:
            return state;
    }
}